import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import styles from "../styles/index.module.css"

export default () => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
      file(relativePath: {eq: "images/profile.png"}) {
        childImageSharp{
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Layout>
      <SEO title="Home" />
      <div style={{ textAlign: `center`}}>
        <Img fluid={data.file.childImageSharp.fluid} className={styles.imgProfile} />
        <h1>Sachie Terada</h1>
        <h4 style={{ marginTop: `.8rem`}}>Front-end Engineer</h4>
      </div>

      <section className={styles.contents}>
        <div className={styles.contents_inner}>
          <h3 style={{ marginBottom: `1rem` }}>Biography</h3>
          <p>
            Hi, thanks for visiting my portfolio site.<br />
            I am Front-end Engineer from Tokyo. I was born and raised in Shizuoka, Japan and moved to Tokyo after graduating from uni.<br /><br />
            From 2019, I started learing front-end development by myself(sometimes with my friend).<br /><br />
            When I was in uni, I studied abroad for about three weeks at Oxford University in the United Kingdom in August 2011.<br /><br />
          </p>
        </div>
      </section>
      <h4>Skills</h4>
      <ul>
        <li>HTML5 / CSS3</li>
        <li>Responsive web design</li>
      </ul>
      <h4>Things I've been learning</h4>
      <ul>
        <li>JavaScript</li>
        <li>Vue.js</li>
        <li>React.js</li>
        <li>Gatsby.js (*this website is made by Gatsby)</li>
      </ul>
    </Layout>
  )
}
